:root {
  --primary-color: #424549;
  --text-color: aliceblue;
}

.App {
  text-align: center;
}

.Main {
 height: 75vh !important;
 width: 100vw !important;
}

body {
  background-color: black;
  color: var(--text-color);
}

button {
  background-color: black;
  color: var(--text-color);
  border-radius: 5px;
}

/* Modal */
.modal {
  border-radius: 5px;
  text-align: center;
  position: fixed;
  background: white;
  width: 80%;
  height: auto;
  top:52%;
  left:50%;
  transform: translate(-50%,-50%);
  background-color:var(--primary-color);
  z-index: 10;
}

.modal-body {
  overflow-y: auto;
  max-height: 69vh;
  padding: 15px;
}

.footer {
  padding: 5px;
}